.menu {
  position: absolute;
  z-index: 10000;
  background: rgba(60, 67, 102, 0.2);
  border-radius: 8px;
  padding: 30px 15px 10px;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 700px;
  width: 340px;
  backdrop-filter: blur(10px);

  &-sub {
    background: rgba(60, 67, 102, 0.2);
    padding: 6px;
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    align-items: center;

    * {
      margin-top: 14px;
      margin-bottom: 10px;
    }

    .graph-control-buttons {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      width: 100%;
      padding: 4px 14px 4px;
      height: 250px;
      margin-bottom: 0;
      margin-top: 0;

      button {
        width: 100%;
        margin: 0;
        font-size: .65em;
        padding-top: 15px;
        padding-bottom: 15px;
      }
    }

    .switch {
      width: 100%;
      margin: 4px 0 4px;
      font-size: .9em;
    }

    .visualization-control-buttons,
    .file-control-buttons {
      width: 100%;
      padding: 4px 14px 4px;
      margin: 0;
      display: flex;
      flex-direction: row;
      justify-content: space-between;

      button {
        margin: 0;
        font-size: .65em;
        padding-top: 15px;
        padding-bottom: 15px;
      }
    }

    &:last-of-type {
      border: 0;
    }

    .algorithms-list, .slider {
      width: 100%;
      padding-left: 14px;
      padding-right: 14px;
      margin-top: 6px;
      margin-bottom: 6px;

      * {
        margin-left: 0;
        margin-right: 0;
      }
    }

    .slider * {
      font-size: .95em;
    }

    .algorithms-list select {
      font-size: .65em;
    }

    .matrix-control-buttons {
      width: 100%;
      padding: 4px 14px 4px;
      margin: 0;

      button {
        margin: 0;
        font-size: .65em;
        padding-top: 15px;
        padding-bottom: 15px;
      }
    }
  }
}

.collapse-button {
  position: absolute;
  top: -5px;
  right: 15px;
  font-size: 1.5em;
  color: #555;

  &:hover {
    cursor: pointer;
    color: #000;
  }
}

.menu-hidden {

  height: 30px;

  * {
    display: none;
  }

  .collapse-button {
    display: block;
  }

